function resetMessages() {
	document.querySelectorAll('.field-msg').forEach(function (f) {
		return f.classList.remove('show');
	});
}

function validateEmail(email) {
	var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
	return re.test(String(email).toLowerCase());
}

function format(d) {
	// `d` is the original data object for the row
	var str = '';
	str += '<table cellpadding="5" cellspacing="0" border="0" style="padding-left:50px;">';
	for (var attr in d) {
		if (attr.slice(0, 4) == 'adam') {
			str += '<tr>' + '<td>' + attr.replace('adam_', '') + ':</td>' + '<td>' + d[attr] + '</td>' + '</tr>';
		}
	}
	str += '</table>';
	return str;
}
