document.addEventListener("DOMContentLoaded", function () {
	/* Formatting function for row details - modify as you need */

	var jobsTable = document.getElementById('myTable');
	var table = new DataTable('#myTable', {
		ajax: jobsTable.dataset.fetchUrl,
		scrollY: 700,
		deferRender: true,
		scroller: true,
		columns: [{
			className: 'dt-control',
			orderable: false,
			data: null,
			defaultContent: ''
		}, { data: 'adam_id' }, { data: 'name' }, { data: 'category' }, { data: 'subcategory' }, { data: 'post' }],
		order: [[1, 'desc']],
		"drawCallback": function drawCallback(settings, json) {
			var togglers = document.querySelectorAll('#myTable td.dt-control');
			for (var i = 0; i < togglers.length; i++) {
				togglers[i].addEventListener('click', function (evt) {
					var tr = evt.target.parentNode;
					var row = table.row(tr);

					// row.child(format(tr.data('child-name'), tr.data('child-value'))).show();
					if (row.child.isShown()) {
						// This row is already open - close it
						row.child.hide();
						tr.classList.remove('shown');
					} else {
						// Open this row
						row.child(format(row.data())).show();
						tr.classList.add('shown');
					}
				});
			}
		}
	});

	// Add event listener for opening and closing details
});

